/* ==========================================================================
   metabox.css loads on any Envira Gallery and Album Create/Edit Screen

   Any styles that will be used by the main Gallery / Album editor screen should be
   added to this file
   ========================================================================== */

/* ==========================================================================
   Imports
   ========================================================================== */
@import "vars";
@import "mixins";
@import "global";

/* ==========================================================================
   Messages
   ========================================================================== */
body.post-type-envira,
body.post-type-envira_album {
    /**
    * Add left and right margins to ensure the message notification horizontally lines up
    * with the metaboxes
    */
    #message {
        margin: 5px 20px 15px 20px;
    }

    #wp-link-wrap{
        z-index: 180105 !important;
    }

    #wp-link-backdrop{
        z-index: 160105 !important;

	}
	#visibility-radio-password{
		display: none;
		visibility: hidden;
	}
	label[for="visibility-radio-password"]{
		display: none;
		visibility: hidden;
		& + br{
			display: none;
		}
	}
	&.envira-protection{
		#visibility-radio-password{
			display: inline-block;
			visibility: visible;
		}
		label[for="visibility-radio-password"]{
			display: inline-block;
			visibility: visible;
			& + br{
				display: block;
			}
		}
	}
}

/* ==========================================================================
   Form
   - The envira-gallery class is assigned to the Gallery and Album Add/Edit screens form.
   ========================================================================== */
form#post.envira-gallery {
    /**
    * Re-establish the left and right margins, so there's spacing between the Admin Menu
    * and the content
    */
    margin: 0 20px;

    /**
    * Tooltip Styles
    */

    .envira-tooltip {
        display:inline-block;
        position:relative;
        text-align:left;
        .dashicons {
            line-height: 30px;
        }
        text-decoration: none;
    }

    .envira-tooltip .top {
        min-width:300px;
        line-height: 30px;
        top:-20px;
        left:50%;
        transform:translate(-50%, -100%);
        padding:5px 10px 15px 10px;
        color:white;
        background-color:black;
        font-weight:normal;
        font-size:12px;
        border-radius:8px;
        position:absolute;
        z-index:99999999;
        box-sizing:border-box;
        box-shadow:0 1px 8px rgba(0,0,0,0.5);
        display:none;
        li {
            width: 100%;
            margin: 1px 0;
            line-height: 13px;
        }
    }

    .envira-tooltip:hover .top {
        display:block;
    }

    .envira-tooltip .top i {
        position:absolute;
        top:100%;
        left:50%;
        margin-left:-12px;
        width:24px;
        height:12px;
        overflow:hidden;
    }

    .envira-tooltip .top i::after {
        content:'';
        position:absolute;
        width:12px;
        height:12px;
        left:50%;
        transform:translate(-50%,-50%) rotate(45deg);
        // background-color:#EEEEEE;
        box-shadow:0 1px 8px rgba(0,0,0,0.5);
    }


    /**
     * Defined so we override WordPress' default styles
     */
    #poststuff {
        /* ==========================================================================
           Envira Gallery Shared Styles for Types and Settings
           ========================================================================== */

        /**
        * Intro Text
        */
        p.envira-intro {
            margin: 0;
            padding: 0 0 30px 0;
            border-bottom: 1px solid #ddd;
            font-size: 16px;
            font-weight: 700;

            small {
                margin: 5px 0 0 0;
                display: block;
                font-weight: 400;

                a {
                    text-decoration: none;
                    font-weight: 600;
                }
            }
        }
        tr.envira-inserted-intro {
            td {
                padding: 0;
                margin: 0;
                .envira-intro {
                    padding-bottom: 10px;
                    margin-bottom: -15px;
                }
            }
        }
        /**
        * Override WordPress default checkboxes
        */
        input[type=checkbox]{
         background-color: $white;
         border: 1px solid $light-grey;
         box-shadow: none;
         margin-right: 10px;


        }
        input[type=checkbox]:checked{
         background-color: $green;
         border-color: $green;
        }
        input[type=checkbox]:checked:before{
            color: $white !important;
        }


        tr#envira-config-additional-copy-box,
        tr#envira-config-additional-copy-box-automatic,
        tr#envira-config-title-caption-automatic-mobile,
        tr#envira-config-title-caption-column-mobile {
          input[type="checkbox"] {
            margin-right: 0;
          }
          label {
            margin-right: 15px;
          }
          label.label-for-checkbox {
            display: inline-block;
          }
        }



        /**
        * Help Video
        * - Can be placed inside p.envira-intro by JS, or outside p.envira-intro when
        * viewing the Images tab for a Dynamic or Default Gallery
        */
        div.envira-video-help {
            position: relative;
            z-index: 1;
            width: 100%;
            margin: 20px 0 0 0;

            iframe {
                position: relative;
                z-index: 1;
                width: 100%;
                height: auto;
                min-height: 300px;
            }

            a.envira-video-close {
                position: absolute;
                z-index: 2;
                top: -12px;
                right: -12px;
                width: 24px;
                height: 24px;
                line-height: 24px;
                @include borderRadius(50%);
                background: #f5f5f5;
                color: #000;
                text-align: center;
                text-decoration: none;
            }
        }

        /**
        * Image Grid
        * - Used on the Images tab for Default Galleries, as well as the Preview Metabox
        */
        ul.envira-gallery-images-output {
            width: 100%;

            li {
                position: relative;
                display: inline-block;
                width: 150px;
                margin: 0 20px 20px 0;
                padding: 0;
                list-style: none;
                vertical-align: top;
                -moz-background-clip: padding;
                -webkit-background-clip: padding-box;
                background-clip: padding-box;
                background: #f7f7f7;

                img {
                    display: block;
                    width: 150px;
                    height: 150px;
                }

                /**
                * If, for some reason, the image doesn't exist or fails to load,
                * the placeholder logo is displayed
                */
                div.placeholder-image {
                    display: block;
                    width: 148px;
                    height: 149px;
                    background: url(images/icons/leaf.svg) center no-repeat;
                    background-size: 64px 64px;
                    border-left: 1px solid #dfdfdf;
                    border-top: 1px solid #dfdfdf;
                    border-right: 1px solid #dfdfdf;
                }

                /**
                * Metadata
                */
                div.meta {
                    width: 148px;
                    overflow: hidden;
                    text-align: center;
                    border-left: 1px solid #dfdfdf;
                    border-bottom: 1px solid #dfdfdf;
                    border-right: 1px solid #dfdfdf;

                    /**
                    * Title
                    */
                    div.title {
                        font-size: 12px;
                        font-weight: 700;
                        width: 138px;
                        height: 18px;
                        line-height: 18px;
                        margin: 8px 5px;
                        overflow: hidden;

                        /**
                        * Hint
                        * - Displays the full title on hover
                        */
                        a.hint {
                            position: absolute;
                            display: inline-block;
                            bottom: 10px;
                            right: 10px;
                            width: 16px;
                            height: 16px;
                            background: #f7f7f7;

                            &.hidden {
                                display: none;
                            }
                        }
                    }

                    /**
                    * Additional metadata
                    */
                    div.additional {
                        display: none;
                        margin: 5px 0 0 0;
                        font-weight: 400;
                    }
                }

                /**
                * Tick Icon
                */
                a.check {
                    display: none;
                    position: absolute;
                    right: 5px;
                    top: 5px;
                    width: 24px;
                    height: 24px;
                    background-color: #eee;
                    -webkit-box-shadow: 0 0 0 1px #fff,0 0 0 2px rgba(0,0,0,0.15);
                    box-shadow: 0 0 0 1px #fff,0 0 0 2px rgba(0,0,0,0.15);

                    div.media-modal-icon {
                        display: none;
                        width: 15px;
                        height: 15px;
                        margin: 5px 0 0 5px;
                        background-position: -21px 0;
                    }
                }

                /**
                * Edit / Delete Buttons
                */
                a.dashicons {
                    position: absolute;
                    display: block;
                    left: 5px;
                    width: 25px;
                    height: 25px;
                    line-height: 25px;
                    font-size: 18px; /* Controls the icon size */
                    outline: none;
                    z-index: 20;
                    border-radius: 2px;
                    -moz-border-radius: 2px;
                    -webkit-border-radius: 2px;

                    &.envira-gallery-modify-image {
                        top: 5px;
                        background: #0085ba;
                        color: $white;
                    }
                    &.envira-gallery-remove-image {
                        top: 5px;
                        left: 35px;
                        background: #e02626;
                        color: $white;
                    }
                    &.envira-active-item,
                    &.envira-draft-item{
                        bottom: 40px;
                        color: $white;
                        span:before{
                            font-size: 16px;
                            // vertical-align: middle;
                        }
                    }
                    &.envira-active-item{
                        background-color: #74b445;
                    }
                    &.envira-draft-item{
                        background-color: #ffa93b;

                    }
                }

                /**
                * Selected state
                */
                &.selected {
                    width: 148px;
                    border: 2px solid $green !important;

                    a.check {
                        display: block;
                        background-color: $green;
                        -webkit-box-shadow: 0 0 0 1px #fff,0 0 0 2px $green;
                        box-shadow: 0 0 0 1px #fff,0 0 0 2px $green;

                        div.media-modal-icon {
                            display: block;
                        }
                    }

                    div.meta {
                        width: 146px;
                    }
                }

                /**
                * Multiple selection sorting - stacks images when the user has selected more than one image + dragged them
                */
                &.ui-sortable-helper {
                    li {
                        position: absolute;
                        top: 0;
                        left: 0;
                    }
                }
            }

            /**
            * List View
            */
            &.list {
                li {
                    position: relative;
                    z-index: 2;
                    display: inline-block;
                    width: 100%;
                    margin: 0 0 10px 0;
                    padding: 10px 15px;
                    background: #fff;
                    border: 1px solid #ddd;
                    -webkit-box-sizing: border-box;
                    -moz-box-sizing: border-box;
                    box-sizing: border-box;
                    border-radius: 2px;
                    -moz-border-radius: 2px;
                    -webkit-border-radius: 2px;


                    img {
                        display: block;
                        width: 75px;
                        margin-left: 45px;
                        height: auto;
                        float: left;
                    }

                    /**
                    * Metadata
                    */
                    div.meta {
                        position: relative;
                        z-index: 1;
                        left: 0;
                        margin: 0;
                        display: inline-table;
                        width: 75%;
                        padding: 0 80px 0 30px;
                        border: none;
                        font-weight: 700;
                        text-align: left;
                        -webkit-box-sizing: border-box;
                        -moz-box-sizing: border-box;
                        box-sizing: border-box;
                        height: auto;

                        /**
                        * Title
                        */
                        div.title {
                            display: block;
                            width: 100%;
                            height: auto;
                            font-size: 16px;
                            margin: 0;
                            padding: 0;
                            float: left;

                            a.hint {
                                display: none;
                            }
                        }

                        /**
                        * Additional metadata
                        * - Displayed in list view
                        */
                        div.additional {
                            /* to do: remove !important; */
                            position: relative !important;
                            display: inline-block;
                            height: 25px;
                            margin: 5px 0 0 0;
                            padding: 0;
                            max-height: 25px;
                            height: 25px;
                            bottom: auto !important;
                            right: auto !important;
                            span.envira-video-type.vimeo {
                                background-color: transparent !important;
                            }
                            span.envira-video-type.videopress {
                                height: 25px !important;
                            }
                        }
                    }

                    /**
                    * Checkbox
                    * - Always display
                    */
                    a.check {
                        display: block;
                        position: absolute;
                        left: 15px;
                        top: 35px;
                    }
                    a.dashicons {

                        &.envira-gallery-remove-image {
                          left: auto;
                          top: 10px;
                          right: 10px;
                        }

                        &.envira-gallery-modify-image {
                            left: auto;
                            top: 10px;
                            right: 40px;
                        }
                        &.envira-active-item.grid-status,
                        &.envira-draft-item.grid-status{
                            display: none;
                        }
                    }
                    .envira-active-item.list-status,
                    .envira-draft-item.list-status{
                        display: block;
                        margin-top: 10px;
                        font-weight: 600;
                        color: black;
                        text-decoration: none;
                        &:hover{
                            text-decoration: none;
                            color: black;
                        }

                    }
                    .envira-active-item.list-status{
                        span{
                            margin-left: 5px;
                            border-radius: 3px;
                            padding: 3px 5px;
                            background-color: #74b445;
                            color: white;
                        }
                    }
                    .envira-draft-item.list-status{
                        span{
                            margin-left: 5px;
                            border-radius: 3px;
                            padding: 3px 5px;
                            background-color: #ffa93b;
                            color: white;
                        }
                    }
                }
            }
            /**
            * Grid View
            */
            &.grid{

            }
        }

        /* ==========================================================================
           Envira Gallery Type
           Envira Album Type
           ========================================================================== */
        #envira-gallery {
            margin: 60px 0 20px 0;
        }
        #envira-gallery,
        #envira-albums {
            /**
            * Hide the title bar of the metabox, so we have a seamless style
            */
            .handlediv {
                display: none;
            }
            .hndle {
                display: none;
            }

            /**
            * Tabs
            * - Move up vertically so they sit above the metabox
            */
            h2.nav-tab-wrapper {
                margin: -56px 0 0 -1px; /* The height of the tab */
                padding: 0;
                border-bottom: none;

                /**
                * Increase tab sizes and style
                */
                .nav-tab {
                    display: inline-block;
                    width: 49.7%;
                    margin: 0;
                    padding: 15px 0;
                    text-align: center;
                    font-size: 16px;
                    border-color: #e5e5e5; /* Matches the metabox border for a seamless UI */
                    background: #f5f5f5;

                    /**
                    * Envira Gallery Icon
                    */
                    &.nav-tab-native-envira-gallery {
                        span {
                            display: inline-block;
                            background: url(images/icons/leaf.svg) 0 5px no-repeat;
                            background-size: 14px 14px;
                            text-indent: 20px;
                        }
				}

                    /**
                    * External Gallery Tab
                    */
                    &.nav-tab-external-gallery {
                        margin-right: -1px;
                    }

                    /**
                    * Hover and Active States
                    */
                    &:hover,
                    &.envira-active {
                        background: $white;
                        border-bottom: 1px solid $white;
                    }

                    /**
                    * Focus State
                    * - Remove WordPress' blue focus box from tabs
                    */
                    &:focus {
                        -webkit-box-shadow: none;
                        -moz-box-shadow: none;
                        box-shadow: none;
                    }

                    &:last-child {
                        float: right;
                    }

                    /**
                    * Hides the radio input on a label
                    * The radio input will be selected when the label is clicked
                    */
                    input[type=radio] {
                        display: none;
                    }
                }
            }

            /**
            * Tab Settings
            */
            .inside {
                margin: 0;
                padding: 0;

                /**
                * Native Envira Gallery Uploader / Select Files
                * - Styles the pluploader instance to look the way we want it
                * - Also see responsive styles at the end of this file
                */
                #envira-gallery-native {
                    #plupload-upload-ui {
                        /* Reserves the space needed for the drag-drop-area, which is loaded using JS */
                        height: 210px;

                        /**
                        * A drag-drop-area div is always output, whether we're on a touch
                        * device or not. WordPress will only style this div if its parent
                        * has the .drag-drop class (i.e. touch devices won't get any CSS styling)
                        *
                        * The below CSS ensures that the drag drop area looks consistent
                        * regardless of device used, and then adjusts the layout if drag-drop is supported.
                        */
                        #drag-drop-area {
                            display: none;
                            border: 4px dashed #b4b9be;
                            height: 200px;

                            .drag-drop-inside {
                                margin: 0 auto 0 auto;

                                p {
                                    display: block;
                                    text-align: center;
                                    color: #a0a5aa;
                                    position: absolute;
                                    top: 50%;
                                    left: 50%;
                                    margin: -10px 0 0 -10px;

                                    /**
                                    * "Drop Files here"
                                    */
                                    &.drag-drop-info {
                                        display: none;
                                        font-size: 20px;
                                    }
                                    &.drag-drop-buttons {
                                        text-align: center;
                                        position: relative;
                                        top: 20px;
                                        left: 0;
                                        margin: 0;
                                    }
                                }
                            }
                        }

                        /**
                        * Select Files from Other Sources Button
                        */
                        a.envira-media-library.button {
                            display: none;
                            position: absolute;
                            margin: 0 auto;
                            left: 280px;
                            right: 0;
                            width: 230px;
                            top: 115px;
                            text-align: center;
                        }

                        /**
                        * When the uploader supports drag and drop, a .drag-drop
                        * class is appended to #drag-drop-area by WordPress
                        *
                        * We use this to show certain descriptions
                        */
                        &.drag-drop {
                            #drag-drop-area {
                                .drag-drop-inside {
                                    width: 520px;
                                    margin: 50px auto 0 auto;

                                    p {
                                        position: relative;
                                        top: 0;
                                        left: 0;
                                        margin: 0;
                                    }

                                    p.drag-drop-info {
                                        display: block;
                                    }

                                    p.drag-drop-buttons {
                                        margin: 10px 0 0 0;
                                        text-align: left;
                                    }
                                }


                            }
                        }

                        /**
                        * Upload Progress Bar
                        */
                        .envira-progress-bar {
                            display: none;
                            width: 100%;
                            position: relative;
                            height: 10px;
                            width: 100%;
                            margin: 10px auto;
                            border-radius: 10px;
                            background: #dfdfdf;
                            background: rgba(0,0,0,.1);

                            .envira-progress-bar-inner {
                                height: 10px;
                                min-width: 20px;
                                width: 0;
                                background: #aaa;
                                background: rgba(0,0,0,.2);
                                border-radius: 10px;
                                -webkit-transition: width 300ms;
                                -moz-transition: width 300ms;
                                -ms-transition: width 300ms;
                                -o-transition: width 300ms;
                                transition: width 300ms;
                            }

                            .envira-progress-bar-status {
                                clear: both;
                                position: absolute;
                                right: 0;
                                width: 50%;
                                height: 30px;
                                margin: 12px 0 0 0;
                                text-align: right;

                                .uploading {

                                }
                                .done {
                                    display: none;
                                }
                                .uploading_zip {
                                    display: none;
                                }
                                .opening_zip {
                                    display: none;
                                    .spinner {
                                        visibility: visible;
                                        margin-top: 0;
                                    }
                                }
                                .done_zip {
                                    display: none;
                                }
                            }


                        }

                        /**
                        * Upload Progress Bar
                        */
                        .envira-progress-adding-images {
                            display: none;
                            width: 100%;
                            position: relative;
                            height: 10px;
                            width: 100%;
                            margin: 5px auto;
                            border-radius: 10px;
/*                            background: #dfdfdf;
                            background: rgba(0,0,0,.1);
*/
                            .envira-progress-bar-inner {
                                height: 10px;
                                min-width: 20px;
                                width: 0;
/*                                background: #aaa;
                                background: rgba(0,0,0,.2);*/
                                border-radius: 10px;
/*                                -webkit-transition: width 300ms;
                                -moz-transition: width 300ms;
                                -ms-transition: width 300ms;
                                -o-transition: width 300ms;
                                transition: width 300ms;*/
                            }

                            .envira-progress-status {
                                clear: both;
                                position: absolute;
                                right: 0;
                                width: 50%;
                                height: 30px;
                                margin: 12px 0 0 0;
                                text-align: right;
                                color: green;
                                -webkit-transition: 0.05s color ease-in-out;
                                transition: 0.05s color ease-in-out;

                                .spinner {
                                    float: right;
                                    margin-top: 0;
                                    visibility: visible;
                                }

                                .adding_images {
                                    float: right;
                                    display: inline-block;
                                }
                            }

                        }

                        p.upload-flash-bypass {
                            display: none;
                        }
                    }
                }

                div.envira-tab {
                    padding: 20px;

                    /**
                    * External Gallery Tab Area
                    */
                    &#envira-gallery-external {
                        text-align: center;

                        /**
                        * Header
                        */
                        p.envira-intro {
                            margin: 0 0 20px 0;
                            padding: 0;
                            border: none;
                        }

                        /**
                        * External Gallery Types
                        */
                        ul#envira-gallery-types-nav {
                            margin: 0 0 40px 0;
                            text-align: center;

                            li {
                                display: inline-block;
                                margin: 0 20px 0 0;
                                vertical-align: top;

                                &:last-child {
                                    margin: 0;
                                }

                                /**
                                * Pro uses a <label> element so the user can select the Gallery Type
                                * Lite uses an <a> element so the user can go to the Upgrade URL
                                */
                                label,
                                a {
                                    float: left;
                                    width: 110px;

                                    input {
                                        display: none;
                                    }
                                    div.icon {
                                        width: 110px;
                                        height: 110px;
                                        margin: 0 0 10px 0;
                                        border: 1px solid #ddd;
                                        background-size: 64px 64px;
                                        background-position: center;
                                        background-repeat: no-repeat;
                                    }
                                    div.title {
                                        font-weight: 700;
                                    }
                                }

                                /**
                                * Hover State
                                * Selected State
                                */
                                &:hover,
                                &.envira-active {
                                    label,
                                    a {
                                        div.icon {
                                            background-color: #f7fcf3;
                                            border-color: #7cc048;
                                        }
                                    }
                                }

                                /**
                                * Featured Content
                                */
                                &#envira-gallery-type-fc {
                                    label,
                                    a {
                                        div.icon {
                                            background-image: url(../images/types/fc.png);
                                        }
                                    }
                                }

                                /**
                                * Instagram
                                */
                                &#envira-gallery-type-instagram {
                                    label,
                                    a {
                                        div.icon {
                                            background-image: url(../images/types/instagram.png);
                                        }
                                    }
                                }
                            }
                        }

                        /**
                        * Upgrade Button
                        */
                        a.button.button-x-large {
                            margin-top: 30px;
                        }
                    }
                }
            }
        }

        /* ==========================================================================
           Envira Albums Drag & Drop Area
           ========================================================================== */
        #envira-albums {
            .inside {
                #envira-types {
                    #envira-album-native {
                        position: relative;

                        /**
                        * Galleries (Drop Target)
                        */
                        ul#envira-album-drag-drop-area {
                            z-index: 2;
                            min-height: 200px;
                            margin: 0;
                            padding: 20px;
                            border: 4px dashed #b4b9be;
                            -webkit-box-sizing: border-box;
                            -moz-box-sizing: border-box;
                            box-sizing: border-box;

                            li {
                                cursor: move;
                            }
                        }

                        /**
                        * Drag & Drop Instructions
                        */
                        p.drag-drop-info {
