                border-left: 1px solid #ddd;

                /**
                * Headings
                */
                h2 {
                    clear: none; // Prevents config layouts breaking in WordPress 4.3 and older
                }

                /**
                * All Tabs
                */
                table.form-table {
                    clear: none;
                    margin: 0 0 40px 0;

                    &.no-margin {
                        margin: 0;
                    }
                    &.no-bottom-margin {
                        margin-bottom: 0;
                    }

                    /**
                    * Row padding
                    */
                    th,td {
                        padding-top: 20px;
                        padding-bottom: 20px;
                    }

                    /**
                    * Sub Headings
                    * - Used to denote an Addon's settings within a settings tab e.g. the Mobile tab
                    */
                    tr.sub-heading {
