                    * - Show move cursor
                    * - Hide Edit & Delete icons, as these are only displayed when the gallery is assigned to the Album
                    */
                    ul.envira-gallery-images-output {
                        li {
                            cursor: move;

                            a.envira-gallery-remove-image,
                            a.envira-gallery-modify-image {
