.envira-notice {
	position: relative;
	margin: 0 0 20px 0;
    padding: 20px;
    @include borderRadius(2px);

    /**
    * Heading
    */
    p.envira-intro {
        border: none !important;
        margin: 0 0 20px 0 !important;
        padding: 0 !important;
	}

	.dashicons.star {
		color: #ff982d;
		line-height: 1.3;
		font-size: 15px;
		width: 15px;
		height: 15px;
	}

    /**
    * Close Button
    */
    .notice-dismiss {
        top: 10px;
        right: 10px;
        color: $white;

        &:before {
            color: $white;
            width: 26px;
            height: 26px;
            font-size: 22px;
        }
	}

    /**
    * Layout
    */
	.no-margin-top {
		margin-top: 0;
	}

    /**
    * Success
    */
    &.success {
        background: $green;
        color: $white;
        border: none;

        /**
        * Button background color needs to be adjusted so it displays over the green background
        * of the success message
        */
        .button.button-primary {
          background-color: $green_alt;
          border-color: $green_alt;
        }
    }

    /**
    * Warning
    */
    &.warning {
        background: #fcf8e3;
        border: 1px solid #faebcc;

        .notice-dismiss {
          color: $black;

          &:before {
               color: $black;
          }
        }
    }

    /**
    * Error
    */
    &.error {
        border-top: 1px solid #dc3232;
        border-right: 1px solid #dc3232;
        border-bottom: 1px solid #dc3232;
    }
}
