    form#post.envira-gallery {
        #poststuff {
            #envira-gallery-settings,
            #envira-albums-settings {
                .inside {
                    /* arrange this area a bit differently */
                    #envira-tabs #envira-tab-images nav.envira-tab-options {
                        ul.envira-right-options {
                            li {
                                display: block;
                                &.filter-images {
                                    width: 100%;
                                    input {
                                        max-width: 100%;
                                        width: 100%;
                                    }
                                }
                                &.sorting-options {
                                    width: 100%;
                                    input {
                                        max-width: 100%;
                                        width: 100%;
                                    }
                                    margin-left: -5px;
                                }
                                &.sorting-directions {
                                    width: 50%;
                                    input {
                                        max-width: 50%;
                                        width: 50%;
                                    }
                                    margin-left: -5px;
                                }
                                &.grid-view,
                                &.list-view {
                                    display: inline-block;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

